import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, Content } from 'ionic-angular';

@Component({
  selector: 'page-big',
  templateUrl: 'big.html',
})
export class BigPage {
  pic1 : any = './assets/img/blank.jpg';
  pic2 : any = './assets/img/blank.jpg';
  @ViewChild('imageCanvas') canvas: any;
  @ViewChild('newOne') newOne: any;

 @ViewChild(Content) content: Content;
  @ViewChild('fixedContainer') fixedContainer: any;
 
  canvasElement: any;
  newElement : any;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidEnter() {
       let itemHeight = this.fixedContainer.nativeElement.offsetHeight;
    let scroll = this.content.getScrollElement();
 
    // Add preexisting scroll margin to fixed container size
    itemHeight = Number.parseFloat(scroll.style.marginTop.replace("px", "")) + itemHeight;
    scroll.style.marginTop = itemHeight + 'px';

    this.doCanv(299);
  }


  ionViewDidLoad() {
   this.canvasElement = this.canvas.nativeElement;
   this.newElement = this.newOne.nativeElement; 
   this.pic1 = this.navParams.get('pic1');
   this.pic2 = this.navParams.get('pic2');
  }

  canv(ev) {
   var canvasPosition = this.canvasElement.getBoundingClientRect();
 
   let x = ev.touches[0].pageX - canvasPosition.x;
   this.doCanv(x); 
  }

  doBefore() {
    this.doCanv(0);
  }

  doAfter() {
    this.doCanv(299);
  }
 
  doCanv(a) {
       var c = this.canvasElement.getContext("2d");
  c.clearRect(0,0,300,20);
  c.fillStyle="#969696";
  c.fillRect(0,1,300,18);
  c.fillStyle="#0A0A0A";
  c.fillRect(a-4,1,10,18);
  a/=299;
  let aa = a.toFixed(2);
     this.newElement.style.opacity = aa;
  }    
}


