import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { BigPage } from '../big/big';

import { Camera, CameraOptions } from '@ionic-native/camera';

import { HTTP } from '@ionic-native/http';


@Component({
  selector: 'page-result',
  templateUrl: 'result.html',
})
export class ResultPage {

 cards : any = [];
 me : any = '';
 instr : any = '';
 pl : any;
 th : any;
 cnt: any =0;
  constructor(public navCtrl: NavController, public navParams: NavParams, private http : HttpClient, private iab : InAppBrowser, private camera: Camera, public loadingCtrl: LoadingController, public nhttp: HTTP, public alertCtrl: AlertController) {
     this.th={ 'pic' : './assets/img/blank.jpg',
               'pic2': './assets/img/blank.jpg',
               'text' : '',
               'text2' : '',
               'ebook' : 0,
               'url' : ''
             };
      this.cnt=0;
              this.pl = {
      picdata : '',
      region: '0', 
      id : 0
    };
    
  }

  ionViewDidLoad() {
    let that=this;
        let home = window.localStorage.getItem('home');
        if (!home) home='https://mysmilesim.dental';

       let url = home + '/server/mysims2.php?region='+window.localStorage.getItem('region')+'&me='+window.localStorage.getItem('uniq')+'&rnd='+Math.random();
// console.log(url);
        this.http.get(url).subscribe(
         data => {
      	let s = JSON.stringify(data);
// console.log(s);
                let d = JSON.parse(s);
            that.cards = d.cards;
            that.instr = d.instr;
            if (that.cards.length) 
               that.th=that.cards[0];
          console.log('here');
         },
         err => {
         }
        ); 

  }

  doebook() {
      const browser = this.iab.create(this.th.url,'_blank','location=yes');
  }

  showbig()
  {
   this.navCtrl.push( BigPage, { pic1 : this.th.pic, pic2 : this.th.pic2});

  }

  gonext()
  {  if (++this.cnt>=this.cards.length) 
       this.cnt=0;
     this.th=this.cards[this.cnt];
  }

  retake(i)
  {  this.pl.id=i; 
     const options: CameraOptions = {
        quality: 100,
        targetWidth:500,
        targetHeight:650, 
        correctOrientation:true,
        destinationType: this.camera.DestinationType.DATA_URL,
        cameraDirection: this.camera.Direction.FRONT,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE
      }
      this.camera.getPicture(options).then((imageData) => {
        this.pl.picdata = imageData;
        this.pl.region = window.localStorage.getItem('region');
        this.saveme();
      }, (err) => {
         alert(JSON.stringify(err));
      });
 
  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you - All Done',
      subTitle: 'Your dentist will be in touch shortly',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'btt',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  saveme()
  {
      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
        let home = window.localStorage.getItem('home');
        if (!home) home='https://mysmilesim.dental';

       let url = home+'/server/retake.php';

         this.nhttp.post(url, that.pl, {})
         .then(data => {
            loading.dismiss();
 	    that.doComplete();
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
     

  }


}



