import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';

import { HTTP } from '@ionic-native/http';

@Component({
  selector: 'page-invite',
  templateUrl: 'invite.html',
})
export class InvitePage {

 public pl : any;
 public welcome : '';

  constructor(public navCtrl: NavController, public navParams: NavParams, public nhttp : HTTP, public loadingCtrl: LoadingController, public alertCtrl : AlertController, private http : HttpClient) {
  this.pl = { 
      did : '0', 
      nam : '',
      eml : '',
      phn : '',
      meta: ''
    }; 

 }

  ionViewDidLoad() { 
        let that=this;
        let home = window.localStorage.getItem('home');
        if (!home) home='https://mysmilesim.dental';
 let uniq = window.localStorage.getItem('uniq');
   
       let url = home + '/server/getdentist.php?me='+uniq+'&rnd='+Math.random();
        this.http.get(url).subscribe(
         data => {
      	let s = JSON.stringify(data);
                let d = JSON.parse(s);
            that.pl.did = d.did;
            that.welcome = d.welcome;
            that.pl.meta = d.meta;
         },
         err => {
         }
        ); 
  }


  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Invitation Sent - Thank you',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'btt',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  saveme()
  {   let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let home=window.localStorage.getItem('home');
       if (!home) home='https://mysmilesim.dental';
       let url = home+'/server/invite.php';

         this.nhttp.post(url, that.pl, {})
         .then(data => {
            loading.dismiss();

            that.doComplete();
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
     

}

}



