import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';

/**
 * Generated class for the ExamplesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-examples',
  templateUrl: 'examples.html',
})
export class ExamplesPage {

  orig : any = './assets/ex/blank.png';
  result : any = './assets/ex/blank.png';
  current : any = 1;
  num : any = 5;

  constructor(public navCtrl: NavController, public navParams: NavParams, private http : HttpClient) {
      this.current=1;
      this.num=5;
  }

  ionViewDidLoad() {
    this.current = 1;
    this.showex();
  }

  goprev() {
    let c = this.current - 1;
    if (c<1) c=this.num ;
    this.current=c;
    this.showex();
  }

  gonext() {
    let c = this.current + 1;
    if (c>this.num) c=1;
    this.current=c;
    this.showex();
  }

  showex()
  { 
    this.orig = './assets/ex/o'+this.current.toString()+'.png';
    this.result = './assets/ex/r'+this.current.toString()+'.png';
  }
}



